}

.segmented-control {
  margin: 5px 0 0 0;
  border: 1px solid #ccc;
  font-size: 0.8em;
  display: inline-block;
  border-radius: 4px;
}

.segmented-control .segmented-control__option {
  display: inline-block;
  float: right;
}

.segmented-control .segmented-control__option__button {
  display: block;
  padding: 3px 8px;
  margin: 0;
  border-right: 1px solid #ccc;
  border-left: 1px solid transparent;
  position: relative;
  z-index: 10;
  cursor: pointer;
}

.segmented-control :first-child .segmented-control__option__button {
  border-right: 1px solid transparent;
  border-radius: 0 4px 4px 0;
}

.segmented-control :last-child .segmented-control__option__button {
  border-radius: 4px 0 0 4px;
}

.segmented-control .segmented-control__option__input {
  position: absolute;
  visibility: hidden;
}

.segmented-control .segmented-control__option__input:checked + .segmented-control__option__button {
  background: #007fd0;
  color: #fff;
  margin: -1px;
  border: 1px solid #007fd0;
  z-index: 15;
}

.select-control {
  font-size: 0.8em;
}

.select-control .select-control__option {
  display: block;
  margin: 0 0 5px 0;
  padding: 0;
}

.select-control .select-control__option__button {
  display: block;
  padding: 3px 8px;
  margin: 5px 0 0 10px;
  background: #f7f7f7;
  cursor: pointer;
  border-radius: 4px;
}

.select-control .select-control__option__button.icon:before {
  color: rgba(0, 0, 0, 0.4);
}

.select-control .select-control__option__input {
  position: absolute;
  visibility: hidden;
}

.select-control .select-control__option__input:checked + .select-control__option__button {
  background: #ccc;
}

.select-control .select-control__option__input:checked + .select-control__option__button.icon:before {
  color: rgba(0, 0, 0, 0.6);
}

